/* By default, emoji images should match the font-size of its parent */
img.emoji {
  height: 1em;
}
.compass-dial-container {
  display: flex;
  align-items: center;
  justify-content: center;
  position: absolute;
  width: 100%;
  height: 100%;
  text-shadow: none;
}

.compass-dial {
  position: relative;
  margin: 0;
  padding: 0;
  width: 100%;
  height: 100%;
  background-image: url(/the-emoji-compass/build/static/media/compass_v01.11d34872.svg);
  background-position: center center;
  background-repeat: no-repeat;
  background-size: 100%;
}

.compass-dial-emoji {
  position: absolute;
  top: 50%;
  left: 50%;
  max-width: 100%;
  font-size: 5vmin;
  color: black;
  width: 1.5em;
  height: 1.5em;
  margin: -0.75em;
}

.compass-dial-emoji > span {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  height: 100%;
  border-radius: 50%;
  -webkit-transition: 0.15s;
  transition: 0.15s;
}

.compass-dial-emoji-highlighted > span {
  -webkit-transform: scale(1.5, 1.5);
          transform: scale(1.5, 1.5);
  background-color: rgba(255, 255, 255, 0.15);
}

.compass-container {
  display: flex;
  position: relative;
  height: 106vmin;
  width: 106vmin;
  margin-left: -3vmin;
  flex: 3 1;
}

.text-container {
  text-align: center;
  color: white;
  flex: 1 1;
  display: flex;
  flex-direction: column;
  text-shadow: 0 0 6px rgba(0,0,0,1);
}

.text-box {
  display: flex;
  flex: 1 1;
  align-items: center;
  justify-content: center;
  flex-direction: row;
  padding: 2em;
  padding-top: 0;
}

.instruction-text {
  font-size: 1.75em;
}

.flavor-text-emoji {
  font-size: 24vmin;
  line-height: 24vmin;
  color: white;
}

.flavor-text-description {
  margin-left: 1.5em;
  color: #fff;
  font-size: 1.15em;
  text-align: left;
  line-height: 1.4;
}

.emoji-requested {
  text-align: center;
  font-size: 12vmin;
  height: 20vmin;
}

.emoji-requested span:not(:last-child) {
  margin-right: 0.5em;
}

.final-text {
  display: flex;
  flex-direction: column;

  font-size: 8vmin;
  color: white;
  text-align: center;
  line-height: 1.2;

  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;

  padding-top: 2rem;
}

@media (orientation: 'landscape') {
  .final-text { 
    font-size: 5vmin;
  }
}

.answer-arc-container {
  position: relative;
}

.answer-text {
  flex: 1 1;
  display: flex;
  align-items: center;
  margin: 0.5em 2em;
  font-size: 6.5vmin;
  font-weight: normal;
  line-height: 1.6;
  text-shadow: 0 0 6px rgba(0,0,0,1);
}

.answer-emojis {
  display: flex;
  justify-content: center;

  position: absolute;
  top: 37%;
  width: 100%;

  /* disable events so svg handlers work */
  pointer-events: none;
}

.answer-emojis > span {
  flex: 1 1;
  margin-left: 0.5em;
  margin-right: 0.5em;
  font-size: 200%;
}

/* Raise the middle one */
.answer-emojis > span:nth-child(2) {
  margin-top: -6%;
}

.final-buttons {
  margin-bottom: 2rem;
}

.final-buttons button {
  position: relative;
  appearance: none;
  -moz-appearance: none;
  -webkit-appearance: none;
  border: 0;
  background-color: transparent;
  color: white;
  width: 36px;
  height: 36px;
  font-size: 28px;
  font-family: 'Gentium Book Basic', serif;
  cursor: pointer;
  margin: 0.75em;
  border-radius: 50%;
  outline: none;
}

.final-buttons button > img {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}

.emoji-table {
  margin-top: 1em;
  margin-bottom: 1em;
}

.emoji-table-row > td {
  padding-bottom: 2vmin;
}

.emoji-table-cell {
  vertical-align: middle
}

.emoji-table-description {
  color: #aaa;
  font-size: 4vmin;
  text-align: left;
}

.emoji-table-symbol {
  margin-right: 0.5em;
  font-size: 10vmin;
}

@media (orientation: 'landscape') {
  #final-emoji-1 {
    font-size: 6vmin;
  }

  .emoji-table {
    margin-top: 0.5em;
    margin-bottom: 0.5em;
  }

  .emoji-table-row > td {
    padding-bottom: 1vmin;
  }

  .emoji-table-symbol {
    font-size: 6vmin;
  }
}

