  font-family: 'Gentium Book Basic';
  font-style: normal;
  font-weight: 700;
  src: local('Gentium Book Basic Bold'), local('GentiumBookBasic-Bold'), url('../fonts/GentiumBookBasic-Bold.woff2') format('woff2');
